(function() {
  'use strict';

  var app = angular.module('esfApp');
  app.service('NTStorageService', ntStorageService);

  function ntStorageService($window) {

    this.setLastAccessedTab = function(position) {
      sessionStorage.setItem('lastAccessedTab', position);
    }

    this.getLastAccessedTab = function() {
      return Number(sessionStorage.getItem('lastAccessedTab'));
    }

    this.clearSessionData = function() {
      sessionStorage.removeItem('lastAccessedTab');
    }

    this.setToolVisitedFlag =  function(tool){
      $window.localStorage.setItem(tool, 'visited');
    }

    this.isToolVisited = function(tool){
      return $window.localStorage.getItem(tool) === 'visited';
    }
  }
})();

(function() {
  'use strict';

  var app = angular.module('esfApp');
  app.service('DataLoaderService', DataLoaderService);

  function DataLoaderService($http, $q) {

    this.loadLocalFile = function(location, onSuccess, onFaliure) {
      $http.get(location)
        .then(function(response) {
          if (typeof response.data === 'object') {
            onSuccess(response.data);
          } else {
            onFaliure(response.data);
          }
        }, function(error) {
          onFaliure(error);
        });
    }
  }
})();

(function() {
  'use strict';

  var app = angular.module('esfApp');
  app.service('CommonUIServices', CommonUIServices);

  function CommonUIServices($mdBottomSheet, $mdToast, $mdDialog, $window, $rootScope) {

    this.navBarLogFlag = 0;
    this.showBottomSheet = function(dialogObject, onCancel, onSuccess) {
      $mdBottomSheet.show({
        locals: {
          bundle: dialogObject.bundle,
          onCancel: onCancel,
          onSuccess: onSuccess
        },
        controller: dialogObject.controller,
        templateUrl: dialogObject.view,
        clickOutsideToClose: dialogObject.clickOutsideToClose,
        escapeToClose: dialogObject.escapeToClose
      });
    }

    this.showDialog = function(dialogObject, onCancel, onSuccess) {
      $mdDialog.show({
        locals: {
          bundle: dialogObject.bundle,
          onCancel: onCancel,
          onSuccess: onSuccess
        },
        controller: dialogObject.controller,
        templateUrl: dialogObject.view,
        parent: angular.element(document.body),
        clickOutsideToClose: dialogObject.clickOutsideToClose,
        escapeToClose: dialogObject.escapeToClose
      });
    }


    this.showSimpleToast = function(toastObject) {
      $mdToast.show(
        $mdToast.simple()
        .textContent(toastObject.message)
        .position('bottom left')
        // .positionClass: 'toast-top-right',
        .hideDelay(toastObject.timeout)
        .theme('error-toast')
        .action('X')
        //.hideDelay(180000)
      );
    };

    this.closeDialogOnbackNavigation = function() {
      $rootScope.$on('$locationChangeStart', function(event) {
        // Check if there is a dialog active
        // event.preventDefault();
        if (angular.element(document).find('md-dialog').length > 0) {
          $mdDialog.cancel();
        }
      })
    }
  }
})();

function nativeImagePicker(id) {
  if (ESFApp) {
    //Android code block
    ESFApp.showImagePicker(id);
  } else {
    //iOS code Block
  }
}

function nativeOpenCamera(id) {
  if (ESFApp) {
    //Android code block
    ESFApp.showCamera(id);
  } else {
    //iOS code Block
  }
}

function nativeImagePickerCallback(imageId, data) {
  if (imageId) {
    var scope = angular.element(document.getElementById(imageId)).scope();
    scope.$apply(function() {
      scope.updateImage(imageId, data);
    });
  } else {
    console.error('Scope id missing. Unable to push callback data.');
  }
}

function nativeDataSubmit(id, data) {
  if (ESFApp) {
    //Android code block
    ESFApp.submitPressed(id, JSON.stringify(data));
  } else {
    //iOS Code Block
  }
}

function nativeViewInitCallback(data) {
  var scope = angular.element(document.getElementById('main_container')).scope();
  if(scope) {
    scope.$apply(function() {
      scope.onViewInit(data);
    });
  } else {
    console.error('Scope id missing. Unable to push callback data.');
  }
}

  /** @ngInject */
function InfoDialogController($scope, $mdDialog, bundle) {
  $scope.info = bundle;

  $scope.cancel = function() {
    $mdDialog.cancel();
  };

}

/** Toast Constants **/
const LENGTH_SHORT = 3000;
const LENGTH_LONG = 6000;

/** Error Messages **/
const ERROR_GENERAL_MSG = 'Oops...something went wrong!';

/** Build Type Enum **/

function FloaterObject(id, name, icon, location) {

  this.id = id;
  this.name = name;
  this.icon = icon;
  this.style = location;
  this.onClick = {};
}

function ToastObject(message, timeout) {
  this.message = message;
  this.timeout = timeout;
}

function DialogObject(view, controller) {
  this.view = view;
  this.controller = controller;
  this.bundle;
  this.clickOutsideToClose = true;
  this.escapeToClose = false;
}

